import Cookie from "js-cookie";
import { Base64 } from "js-base64";
// optional style for arrows & dots
export default {
  computed: {
    base_img_url() {
      return process.env.apiPublicUrl;
    },
  },
  methods: {
    getSearchs(param) {
      if (this.search !== null) {
        this.$store
          .dispatch("getSearchs", {
            searchsURL:
              "searchs?page=" +
              this.pagination.current +
              "&search=" +
              decodeURIComponent(this.search),
          })
          .then((response) => {
            this.searchs = this.$store.getters.searchs.data;
            this.pagination.current = this.$store.getters.searchs.current_page;
            this.pagination.total = this.$store.getters.searchs.last_page;
          });
      } else {
        if (param) {
          this.$store
            .dispatch("getSearchs", { searchsURL: param })
            .then((response) => {
              this.searchs = this.$store.getters.searchs.data;
              this.pagination.current = this.$store.getters.searchs.current_page;
              this.pagination.total = this.$store.getters.searchs.last_page;
            });
        } else {
          this.$store
            .dispatch("getSearchs", {
              searchsURL: "searchs?page=" + this.pagination.current,
            })
            .then((response) => {
              this.searchs = this.$store.getters.searchs.data;
              this.pagination.current = this.$store.getters.searchs.current_page;
              this.pagination.total = this.$store.getters.searchs.last_page;
            });
        }
      }
    },
    onPageChange() {
      this.getSearchs();
    },
  },
  data() {
    return {
      searchs: [],
      search: null,
      pagination: {
        current: 1,
        total: 1,
      },
    };
  },
};
